import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { HttpService } from '../../../service/httpService';
import { NativeService } from '../../../service/NativeService';



@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {

  private endPwd:string;
  private timeEvent:any;
  private time = 60;
  private isCountdown = false;
  private getCodeString = "";
  private form = {
    account:'',
    password:'',
    code:'',
    stageType:'mk',
    channelType:'qd01'
  };

  constructor(public navCtrl: NavController,
              public http:HttpService,
              public nat:NativeService,
              public navParams: NavParams) {

  }

  getCode(){

    if (this.form.account.length <= 0){
      this.nat.showToast("!");
      return;
    }
    if (!this.nat.checkPhone(this.form.account)){
      this.nat.showToast("");
      return;
    }
    let url = ['otherWay',"/api/code/sendCode"];
    let map = {
      account:this.form.account,
      stageType:'mk',
      type:'0'
    };
    this.isCountdown = true;
    this.countdownAction();
    let self = this;
    this.http.post(url,map,function (res,err) {
      if(!err){
        self.nat.showToast("");
        console.log(res);
      }
    })
  }
  ionViewWillUnload(){
    if(this.timeEvent){
      clearInterval(this.timeEvent);
    }
  }

  countdownAction(){
    this.getCodeString = this.time + "s";
    this.timeEvent = setInterval( ()=>{
      if(this.time == 0){
        this.time = 60;
        this.isCountdown = false;
        clearInterval(this.timeEvent);
        this.getCodeString = "";
      }else {
        this.time --;
        this.getCodeString = this.time + "s";
      }

    },1000);

  }


  registerAction(){
    var self=this
    let url = ['otherWay','/api/user/userRegrsiter'];
    // let options = {
    //   account:this.form.account,
    //   stageType:0
    // }
    // this.http.post(url,options,function (res,err) {
    //   self.nat.hideLoading();
    //   if(!err){
    //     self.nat.showToast("");
    //     self.nat.storageSetValue("userInfo",JSON.stringify(res));
    //     self.navCtrl.pop();
    //   }
    // });


    if (this.form.account.length <= 0){
      this.nat.showToast("!");
      return;
    }
    if (!this.nat.checkPhone(this.form.account)){
      this.nat.showToast("");
      return;
    }

    if(this.form.password.length < 6){
      this.nat.showToast("6");
      return;
    }
    if(!this.nat.checkPassWord(this.form.password)){
      this.nat.showToast("");
      return;
    }
    if (this.form.password.length > 16){
      this.nat.showToast("16");
      return;
    }
    if(this.endPwd != this.form.password){
      this.nat.showToast("");
      return;
    }

    if(this.form.code.length <= 0){
      this.nat.showToast("");
      return;
    }

    this.nat.showLoading("...");
    this.http.post(url,this.form,function (res,err) {
      self.nat.hideLoading();
      if(!err){
        self.nat.showToast("");
        self.nat.storageSetValue("userInfo",JSON.stringify(res));
        self.navCtrl.pop();
      }
    });

  }

}



