import { Pipe, PipeTransform } from '@angular/core';


@Pipe({
  name: 'transnumber',
})
export class TransnumberPipe implements PipeTransform {
  transform(value: any, ...args) {
    let numberChart=['','','','','','','','','','']
    if(value<=0){
      return ""
    }
    else if(value<=10&&value>0){
      return numberChart[value-1] + ''
    }else if(value>10&&value<60){
      let index = value.toString()[0]
      let index1 = value.toString()[1]
      if(index1=='0'){
        return numberChart[index] + '' + ''
      }else{
        if(index=='1'){
          return '' +  numberChart[index1] + ''
        }else
        return numberChart[index] + '' +  numberChart[index1] + ''
      }
    }else if(value>=60&&value<60*24){
      //return value/
    }
    else{
       return value
     }
  }
}



