import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'custompipe',})
export class CustompipePipe implements PipeTransform {
  /**
   * Takes a value and makes it lowercase.
   */
  transform(value: string, ...args) {
    let number = parseInt(value);
    if(args[0] == '') return number < 0 ? (number * -1) : number;
    if(number >= 0){
      if(args[0] == 'even') return "";
      if(args[0] == 'reliance') return "";
      if(args[0] == 'big') return "";
      if(args[0] == 'small') return "";
      if(args[0] == 'with') return "";
      if(args[0] == 'quality') return "";
      if(args[0] == '0') return "0";
      if(args[0] == '1') return "1";
      if(args[0] == '2') return "2";
      if(args[0] == '3:0') return "3:0";
      if(args[0] == '2:1') return "2:1";
      if(args[0] == '1:2') return "1:2";
      if(args[0] == '0:3') return "0:3";
      if(args[0] == 'shen') return "";
      if(args[0] == 'pin') return "";
      if(args[0] == 'bao') return "";
      if(args[0] == 'sun') return "";
      if(args[0] == 'dui') return "";
      if(args[0] == 'ban') return "";
      if(args[0] == 'za') return "";
      if(args[0] == 'zhus') return "";
      if(args[0] == 'zhul') return "";
      if(args[0] == '') return "";
      if(args[0] == '') return "";
      if(args[0] == '') return "";
      if(args[0] == '') return "";
      if(args[0] == '') return "";
      if(args[0] == 'baozi') return "";
      if(args[0] == 'sbt') return "";
      if(args[0] == 'duizi') return "";
    }
    return number * -1;
  }




  

}



